.filters {
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: center;
  position: sticky;
  top: 20px;
  left: 0;
  z-index: 100;
  padding: 0; }

.item + .item {
  margin-left: 7px; }

.button {
  padding: 5px 15px;
  border: none;
  background: #eee;
  color: #333;
  font-size: 12px;
  font-weight: bold;
  text-transform: uppercase; }
  @media (min-width: 768px) {
    .button {
      font-size: 15px; } }

.buttonActive {
  background: #f14c10;
  color: #fff; }



