.header {
  padding: 20px 0 0;
  text-align: center; }
  @media (min-width: 768px) {
    .header {
      padding: 40px 0;
      text-align: left; } }

.lockup {
  color: inherit;
  text-decoration: none; }

.title {
  margin-bottom: 0;
  font-size: rem(36px);
  line-height: 1;
  font-weight: normal; }
  @media (min-width: 768px) {
    .title {
      font-size: 48px;
      line-height: .8; } }

.tag {
  color: #f14c10;
  font-size: 14px; }
  @media (min-width: 768px) {
    .tag {
      margin: 0;
      font-size: 18px; } }

@media (min-width: 768px) {
  .row {
    display: flex;
    justify-content: space-between;
    align-items: center; } }

.nav {
  font-weight: bold; }

.navList {
  display: flex;
  justify-content: flex-end;
  list-style: none;
  padding: 0;
  margin: 0; }
  .navList + .navList {
    margin-top: 20px; }

.navItem + .navItem {
  margin-left: 20px; }

.navLink {
  display: block;
  color: #f14c10;
  text-align: center; }

.c-nav__list {
  display: flex;
  justify-content: flex-end;
  font-weight: bold; }



